.detailHeader {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.editBtn {
  background-color: var(--blue);
  border-radius: 1rem;

  height: 4rem;
  width: 11.9rem;
}

.editBtn:hover {
  background-color: hsl(230, 91%, 73%);
}
.editBtn:disabled,
.linkDisabled {
  background-color: hsl(0, 0%, 70%);
  cursor: not-allowed;
}

.editLink {
  font-size: 1.3rem;
  font-weight: 700;
  line-height: 1.9rem;
  color: var(--dark-white);
}

/* tablet */
@media only screen and (min-width: 40em) {
  .editBtn {
    height: 4.4rem;
    width: 14.2rem;
  }
  .editLink {
    font-size: 1.4rem;
    line-height: 2rem;
  }
}
